<template>
  <p>Simple call example</p>
  <div class="form">
    <div class="joinF">
      <input type="text" v-model="roomId" placeholder="roomId" >
      <div>
          <button v-on:click="createRoom()" id="try-video-button">
              Create room
          </button>
          <button v-on:click="connectToRoom()" id="stop-video-button">
              Join room
          </button>
          <button class="enable" :class="{connected: isConnected}, {created: isCreated}" id="toggle-video">
            Enable
          </button>
      </div>
    </div>
    <div class="camera">
      <div id="video-div" v-show="localVideo">
        <p>local video</p>
      </div>
      <div id="remote-media-div" v-show="remoteVideo">
        <p>remote video</p>
      </div>
    </div>
    
  </div>
</template>

<script>
// @ is an alias to /src
import Twilio, { connect, createLocalTracks, createLocalVideoTrack, remoteParticipant, currentUser } from 'twilio-video'
import Video from 'twilio-video'
import axios from 'axios'
import $ from "jquery";

export default {
  name: 'HomeView',
  data() {
    return {
      roomId: '',
      TOKEN: '',
      indent: '',
      localVideo: true,
      remoteVideo: true,
      isConnected: false,
      isCreated: false,
      videoTodge: false,
      connectedInd: 0,
      localVideoTrackCreated: false
    }
  },
  mounted() {
  },
  methods: {

    async createRoom() {
      const token = await this.getAccessToken()
      createLocalTracks({
        audio: true,
        video: { width: 640 }
      }).then(localTracks => {
        return connect(token, {
          name: this.roomId,
          tracks: localTracks
        });
      }).then(room => {
        console.log(`Connected to Room: ${room.name}`);

        this.isCreated = true
        room.on('participantConnected', participant => {
          function handleTrackEnabled(track) {
            track.on('enabled', () => {
              console.log(28310982138019832901309890)
            });
          }
          participant.tracks.forEach(publication => {
            if (publication.isSubscribed) {
              const track = publication.track;
              document.getElementById('remote-media-div').appendChild(track.attach());
            }
          });
          room.participants.forEach(participant => {
            participant.tracks.forEach(publication => {
              if (publication.isSubscribed) {
                const track = publication.track;
                document.getElementById('remote-media-div').appendChild(track.attach());
                $("#remote-media-div video").remove();
              }
            });

            participant.on('trackSubscribed', track => {
              document.getElementById('remote-media-div').appendChild(track.attach());
              $("#remote-media-div video").remove();
            });
            $("#remote-media-div video").remove();
          });
          $("#remote-media-div video").remove();
          console.log(`Participant "${participant.identity}" connected`);
          participant.on('trackSubscribed', track => {
            if (this.connectedInd == 2) {
              participant.tracks.forEach(publication => {
                this.connectedInd += 1
                if (publication.isSubscribed) {
                  const track = publication.track;
                  $("#remote-media-div VIDEO").remove();
                  document.getElementById('remote-media-div').appendChild(track.attach());
                  console.log('remote-media')
                }
              });
              this.connectedInd += 1
            } else {
              this.connectedInd += 1
            }
            console.log('new web')
            
          });
          $('.enable').click(function() {
            
            createLocalVideoTrack().then(localVideoTrack => {
              if (this.localVideoTrackCreated != true) {
                Video.createLocalVideoTrack().then(function(videoTrack) {
                  const videoElement = videoTrack.attach();
                  document.getElementById("video-div").appendChild(videoElement);
                });
                this.localVideoTrackCreated = true
              }
              room.localParticipant.videoTracks.forEach(publication => {
                console.log(213123123)
                publication.track.enable();
              });

              room.participants.forEach(participant => {
                participant.tracks.forEach(publication => {
                  if (publication.isSubscribed) {
                    handleTrackEnabled(publication.track);
                  }
                  publication.on('subscribed', handleTrackEnabled);
                });
              });
              return room.localParticipant.publishTrack(localVideoTrack);
            }).then(publication => {
              console.log('Successfully unmuted your video:', publication);
            });
            
          })
          
          
          
        });
      });


    },
    async connectToRoom() {
      const token = await this.getAccessToken()
      connect(token, {
        audio: true,
        name: this.roomId,
        video: { width: 640 }
      }).then(room => {
        console.log(`Connected to Room: ${room.name}`);
        this.isConnected = true

        room.on('participantConnected', participant => {
          participant.tracks.forEach(publication => {
            if (publication.isSubscribed) {
              const track = publication.track;
              document.getElementById('remote-media-div').appendChild(track.attach());
            }
          });
        })
        
        room.participants.forEach(participant => {
          participant.tracks.forEach(publication => {
              if (publication.isSubscribed) {
                const track = publication.track;
                document.getElementById('remote-media-div').appendChild(track.attach());
                $("#remote-media-div video").remove();
              }
            });

            participant.on('trackSubscribed', track => {
              document.getElementById('remote-media-div').appendChild(track.attach());
              $("#remote-media-div video").remove();
            });
          $("#remote-media-div video").remove();
          participant.on('trackSubscribed', track => {
            if (this.connectedInd == 2) {
              $("#remote-media-div audio")
              participant.tracks.forEach(publication => {
                $("#remote-media-div VIDEO").remove();
                const track = publication.track;
                document.getElementById('remote-media-div').appendChild(track.attach());
              });
              this.connectedInd += 1
            } else {
              this.connectedInd += 1
            }
            
          });
          function handleTrackEnabled(track) {
            track.on('enabled', () => {
    
            });
          }
          room.participants.forEach(participant => {
            participant.tracks.forEach(publication => {
              if (publication.isSubscribed) {
                handleTrackEnabled(publication.track);
                
              }
              publication.on('subscribed', handleTrackEnabled);
            });
          });

          $('.enable').click(async function() {
            if ($('.enable').hasClass('connected')) {
              createLocalVideoTrack().then(localVideoTrack => {
                if (this.localVideoTrackCreated != true) {
                  Video.createLocalVideoTrack().then(function(videoTrack) {
                    const videoElement = videoTrack.attach();
                    document.getElementById("video-div").appendChild(videoElement);
                    
                  });
                  this.localVideoTrackCreated = true
                }
                room.localParticipant.videoTracks.forEach(publication => {

                  publication.track.enable();
                });
                function handleTrackEnabled(track) {
                  track.on('enabled', () => {

                  });
                }
                
                
                return room.localParticipant.publishTrack(localVideoTrack);
              }).then(publication => {

              });
              
            }
            
          })
          
        });
      });
    },
    stopVideoOnly(stream) {
      stream.getTracks().forEach((track) => {
          if (track.readyState == 'live' && track.kind === 'video') {
              track.stop();
          }
      });
    },

    async createLocalVideoTrackFunc(room){
      Video.createLocalVideoTrack().then(function(videoTrack) {
        const videoElement = videoTrack.attach();
        document.getElementById("video-div").appendChild(videoElement);
      });
    },
    disableCamera(){
      Video.createLocalVideoTrack().then(function(videoTrack) {
        const videoElement = videoTrack.attach();
        document.getElementById("video-div").appendChild(videoElement);
      });
      
    },
    async getAccessToken() {
      const tokenshik = await axios.get(`http://localhost:3000/token?identity=${Date.now()}`)

      this.TOKEN = String(tokenshik.data.token)
      return String(tokenshik.data.token)
    },
  }
}
</script>

